.AvailableSolutions .service-container{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}



.AvailableSolutions .service{
    background-color: #fefefe;
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 175px;
}




.UltimateSecurity{
    min-height: 250px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: linear-gradient(to right, rgba(0,0,0,.3), rgba(0,0,0,.3)) ,url(../../assets/Depositphotos_Cyber.jpg);
    background-attachment: fixed;
    color: var(--bs-gray-100);
    background-size: cover;
    background-position: center;
}

.GetSophos{
    text-align: center;
}
.GetSophos .statistic{
    color: var(--orangeColor);
    text-align: center;
}
.GetSophos .btn::after{
   content: ' >';
   color: var(--orangeColor);
}



.CloudSolutionsHero{
    min-height: 250px;
    background: linear-gradient(to right, rgba(0,0,0,.5), rgba(0,0,0,.5)), url(../../assets/Depositphotos_Cyber.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}



.CloudOptions h5{
    text-align: center;
    color: var(--bs-gray-700);
}
.CloudOptions .icon{
    color: var(--orangeColor);
}
.CloudOptions h1{
    text-align: center;
}


.CloudPlansBusiness .box, .CloudEnterprise .box{
    width: 300px;
    margin: 30px 10px;
}

.CloudPlansEnterprise .box, .CloudEnterprise .box{
    width: 300px;
    margin: 30px 10px;
}

.CloudPlansBusiness .box h1.price, .CloudPlansEnterprise .box h1.price{
    color: var(--orangeColor);
    position: relative;
}

.CloudPlansBusiness .box h1.price::before, .CloudPlansEnterprise .box h1.price::before{
    content: '$USD' ;
    font-size: 1rem;
    color: var(--bs-gray-900);
}

.CloudPlansBusiness .box h1.price::after, .CloudPlansEnterprise .box h1.price::after{
    content: '/user/month' ;
    font-size: 1rem;
    color: var(--bs-gray-900);
}


.CloudPlansBusiness .box .text-body .available, .CloudPlansEnterprise .box .text-body .available{
    color: green!important;
}

.CloudPlansBusiness .box .text-body .icon, .CloudPlansEnterprise .box .text-body .icon{
    color: var(--orangeColor);
}